import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import login from '@/components/login'
import service from '@/components/service/index'
import work from '@/components/work/index'

Vue.use(Router)

export default new Router({
	routes: [

		{
			path: '/',
			name: 'index',
			components: {'page':index},
			children: [{
				path: 'hello',
				name: 'hello',
				component: resolve => require(['@/components/HelloWorld'], resolve)
			}]
		},
		{
			path: '/login',
			name: 'login',
			component: login
		},
		{
			path:'/service',
			name:'service',
			components:{'page':service},
			children:[{
				path:"create",
				name:'',
				component:resolve => require(['@/components/service/create'],resolve)
			}]
		},
		{
			path:'/work',
			name:'work',
			components:{'page':work},
			children:[{
				path:"create",
				name:'',
				component:resolve => require(['@/components/work/create'],resolve)
			}]
		}

	]
})


