<template>
</template>

<script>
	const URL = 'http://139.224.22.192/api/sso/'
	const API = 'http://139.224.22.192/api/app/'

	var _this='';
	
	var all_overstory_data = {};

	function dataform(data = {}) {
			if(data._this){
			_this=data._this
		}
		return Object.assign(all_overstory_data, data)
	}


function gettoken(){	
		let login = document.cookie;
		let wxlogin = {};
		login = login.split(';');

		login.forEach(item => {
			let name = item.split("=");
			if(name[0] == ' login' || name[0] == 'login') {
				wxlogin.token = name[1]
			};
			if(name[0] == ' corpid' || name[0] == 'corpid') {
				wxlogin.corpKey = name[1]
			}
		})
		
		
		return wxlogin
}

function request(data,p=false) {
	if(_this){
		_this.$Loading.start();
	}
	return new Promise((resolve, reject) => {
		
		
		let token = gettoken();

		if(data.login) {
			data.login = true
		} else {
			data.login = false
		}
		
		let url;
		if(p){
			url=URL
		}else{
			url=API
		}
		
		let options = {
			type: data.method || 'get',
			url: url + data.url,
			async: true,
			headers:token,
			data: data.data?data.data:{},
			dataType: "json",
			success: function(resp) {
				if(_this){
					_this.$Loading.finish()
				}
				
				if(( resp.code == '34001' || resp.errcode=='34001')&& !resp.login) {
					alert('登录已过期')
					let TEST = 'http://localhost:8081/#/login';
					// let APIS = 'http://landa.aosaiban.com/company/#/login'
					let APIS = 'http://139.224.22.192/admin/#/login'
					location.href = process.env.LOCATION;
					return 
				}
				
				if(resp.errmsg!='ok'){
					_this.$Message.info({
						content:resp.errmsg,
						duration:10,
						closable:true
					});
					return 
				}
				
				resolve(resp)
				
			},
			error: function(XMlHttp, textStatus, erroe) {
				reject(textStatus)
				if(_this){
					_this.$Loading.error();
					_this.$Message.error(textStatus)
				}
				console.error(textStatus)
			}
		};
		
		if (data.method == 'post' ) {
			options = Object.assign(options, {
				contentType: "application/json; charset=utf-8",
				data:JSON.stringify( data.data?data.data:{} )
				
			});
		}
		
		$.ajax(options)

	})
}


function jqueryajax(data) {
	if(_this){
		_this.$Loading.start();
	}
	return new Promise((resolve, reject) => {	
		
		let token = gettoken();		
		let options = {
			type: data.method || 'get',
			url: API + data.url,
			async: true,
			data: data.data?data.data:{},
			dataType: "json",
			success: function(resp) {
				if(_this){
					_this.$Loading.finish()
				}
				
				if(resp.errmsg!='ok'){
					_this.$Message.info({
						content:resp.errmsg,
						duration:10,
						closable:true
					});
					return 
				}
				
				resolve(resp)
				
			},
			error: function(XMlHttp, textStatus, erroe) {
				reject(textStatus)
				if(_this){
					_this.$Loading.error();
					_this.$Message.error(textStatus)
				}
				console.error(textStatus)
			}
		};
		
		if (data.method == 'post' ) {
			options = Object.assign(options, {
				contentType: "application/json; charset=utf-8",
				data:JSON.stringify( data.data?data.data:{} )
				
			});
		}
		
		$.ajax(options)

	})
}





function corpApp(data) {
	if(_this){
		_this.$Loading.start();
	}
	return new Promise((resolve, reject) => {
		
		
		let token = gettoken();

	
		let url = 'http://139.224.22.192/api/corp/';
		
		
		let options = {
			type: data.method || 'get',
			url: url + data.url,
			async: true,
			headers:token,
			data: data.data?data.data:{},
			dataType: "json",
			success: function(resp) {
				if(_this){
					_this.$Loading.finish()
				}
				if(( resp.code == '34001' || resp.errcode=='34001') && !resp.login) {
					alert('请登录')
					let TEST = 'http://localhost:8081/#/login';
					// let APIS = 'http://landa.aosaiban.com/company/#/login'
					let APIS = 'http://163.53.168.238:19999/c/#/login'
					location.href = process.env.LOCATION;
					return 
				}
				
				if(resp.errmsg!='ok'){
					_this.$Message.info({
						content:resp.errmsg,
						duration:10,
						closable:true
					});
					return 
				}
				
				resolve(resp)
				
			},
			error: function(XMlHttp, textStatus, erroe) {
				reject(textStatus)
				if(_this){
					_this.$Loading.error();
					_this.$Message.error(textStatus)
				}
				console.error(textStatus)
			}
		};
		
		if (data.method == 'post' ) {
			options = Object.assign(options, {
				contentType: "application/json; charset=utf-8",
				data:JSON.stringify( data.data?data.data:{} )
				
			});
		}
		
		$.ajax(options)

	})
}


function getuserdata() {
	if(all_overstory_data.userdata) {
		return new Promise((resolve,reject) => {
			resolve(all_overstory_data.userdata)
		})
	} else {
		return new Promise((resolve, reject) => {
			request({
				url: 'use/get/current'
			}).then(res => {
				if(res.errcode == '40000' && res.errmsg == 'ok') {
					resolve(res.data)
					all_overstory_data.userdata=res.data
				}else{
					
				}

			})
		})
	}

}

//表单验证 tos 提交数据 ex 验证数据 phone===string 手机号码字段 
function formreg(tos,ex,phone=''){
	for(let key in tos){
		if(tos[key]==''&&ex[key]){
			return _this.$Message.warning(ex[key])
		}
	}
	
	if(phone){
			if(!/^1[3|4|5|6|7|8]\d{9}$/.test(tos[phone])){
				return _this.$Message.warning('手机号码格式不正确')
		}
	}
	return false
}
	export default {
		dataform,
		request,
		getuserdata,
		gettoken,
		jqueryajax,
		corpApp,
		formreg
	}
</script>

