<template>
   		<div class="hello">
   			<router-link  tag='div' to='/login' class="logo">业务商城</router-link>
   			<ul class="nav_left">
   				<router-link  tag='li' to='/' :class='{"on":navdata==""}'>首页</router-link>
   				<router-link tag='li' to='/service' :class='{"on":navdata=="service"}'>服务管理</router-link>
   		<!-- 		<router-link tag='li' to='/work' :class='{"on":navdata=="work"}'>应用开发</router-link> -->
   			</ul>
   			
   			<ul class="nav_left nav_right">
   				<li v-on:click='back'>退出</li>
   			<!-- 	<li class="button_s">开发文档</li> -->
   				<li>{{userData.name}}</li>
   			</ul>
   		</div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
     userData:{},
		 navdata:''
    }
  },
	created(){
	let time_number = 1
	let interval =	setInterval(()=>{
			time_number++
			if(this.DATAFORM.dataform().userdata){
				this.userData = this.DATAFORM.dataform().userdata
				clearInterval(interval)
			}
			if(time_number>40){
				clearInterval(interval)
			}
		},500)
			let route=this.$route;
		let path = route.path;
		path = path.split('/')
		if(path.length>=2){
			this.navdata=path[1]
		}
		
		console.log(path)
	},
    methods:{
    	back(){
				
				// let LOGIN = 'login=;domain=aosaiban.com;path=/';
				// let CORP = 'corpid=;domain=aosaiban.com;path=/';
				
				let LOGIN = 'login=;domain=163.53.168.238;path=/';
				let CORP = 'corpid=;domain=163.53.168.238;path=/';
				
				let TEST_LOGIN = 'login=;path=/;domain=localhost';
				let TEST_CORP = 'corpid=;path=/;domain=localhost';
				
						document.cookie=process.env.COOKIE_LOGIN;
						document.cookie=process.env.COOKIE_CORP;
				this.DATAFORM.dataform({userdata:''})		
    		this.$router.push('/login')
    	}
    },
		watch: {
			'$route' () {
				let route=this.$route;
			let path = route.path;
			path = path.split('/')
			if(path.length>=2){
				this.navdata=path[1]
			}
			}
		}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.hello{
	height: 65px;
	background: #395ea8;
	width: 100%;
	min-width: 1080px;
	padding:0  15px;
	box-sizing: border-box;
}

.hello .logo{
	width: 103px;
	height:100%;
	font-size: 20px;
	color: #fff;
	line-height: 65px;
	text-align: right;
	margin:0 80px 0 0;
	/* background: url(../assets/img/logo.png) center center no-repeat; */
	float: left;
	box-sizing: initial;
	cursor: pointer;
}

.hello .nav_left{
	width: auto;
	height: 100%;
	float: left;
	overflow: visible;
}

.hello .nav_left li{
	font-size: 14px;
	color: #FFFFFF;
	float: left;
	line-height: 63px;
	box-sizing: border-box;
	border-bottom: 2px solid transparent;
	cursor: pointer;
	margin-right: 65px;
}

.hello .nav_left li:hover{
	font-weight: bold;
}

.hello .nav_left .on{
	border-color: #FFFFFF;
	font-weight: bold;
}

.hello .nav_right{
	float: right !important;
	
}

.hello .nav_right li{
	float: right !important;
	margin: 0 30px 0 0 ;
}

.hello .nav_right .button_s{
	width: 80px;
	height: 30px;
	text-align: center;
	line-height: 30px;
	font-size: 12px;
	color: #666666;
	margin-top: 15px;
	background: #FFFFFF;
	border-radius: 3px;
}
</style>



