<template>
	<div class="headerbox">
		<div class="header">
			<div class="logo">业务商城</div>

			<ul class="right_list">
				<li ><!-- <span>入驻</span> --><em style="font-style: normal;font-weight: initial;position: relative;top: -1px;"> | </em><span @click="wxlogin" v-if='login'>登录</span><router-link v-else tag='span' to='/'>管理</router-link></li>
			<!-- 	<li>应用市场</li> -->
				<li>开发文档</li>
				<!-- <li>合作案例</li> -->
				<li class="on">应用市场</li>
			</ul>
		</div>
	</div>
</template>

<script>
	let TEST = 'http://localhost:8081/#/login';
	// let APIS = 'http://landa.aosaiban.com/company/#/login'
	
	let APIS = 'http://139.224.22.192/c/#/login'

	export default {
		name: 'headerbox',
		data() {
			return {
				navdata: '',
				login:true
				
			}
		},
		created() {
			let route = this.$route;
			this.navdata = route.name;
			this.gettoken()
			
			
			
		},
		methods: {
			wxlogin(){
				location.href = process.env.LOCATION
			// location.href = 'http://localhost:8081/#/login'
			},
			gettoken(){
				let wxlogin = this.DATAFORM.gettoken();
				console.log(wxlogin)
				if(wxlogin.token&&wxlogin.corpKey){
					this.login=false
				}
			}
		},
		watch: {
			'$route' () {
				let route = this.$route;
				this.gettoken()
				if(route.name) {
					this.navdata = route.name
				}
			}
		}
	}
</script>

<style>
	@import '../assets/css/index.css';
</style>


