<template>
	<div class="service_content">
		<div class="service_header">
			<Button class="button_s" @click="add_app">添加应用</Button>
		</div>
		
		<div>
			<Table  ref='contentlist'  width='100%' :columns='content' :data='content_data.data' ></Table>
		</div>
		<div style="margin-top: 10px;overflow: auto;">
			<Page :total='content_data.total' :page-size='10' style='float:right' @on-change='pages'></Page>
		</div>
		
			<transition name='cardshow'>
		<router-view></router-view>
			</transition>
	</div>
</template>

<script>
	export default {
		name: 'work',
		data() {
			return {
				page:0,
				content:[{
					key:'id',
					title:'id',
					align:'center'
				},{
					key:'logo',
					title:'图标',
					width:100,
					align:'center',
					render:(h,p) =>{
						return h('Avatar',{
							props:{
								src:p.row.logo
							}
						})
					}
				},{
					key:'name',
					align:'center',
					title:'名称'
					
				},{
					key:'homepageUrl',
					title:'微应用首页',
					align:'center',
					render:(h,p) =>{
						return h('a',{
						domProps:{
							href:p.row.homepageUrl
							}
						},p.row.homepageUrl)
					}
				},{
					key:'ompLink',
					title:'微应用后台',
					align:'center',
					render:(h,p) =>{
						return h('a',{
						domProps:{
							href:p.row.ompLink
							}
						},p.row.ompLink)
					}
				},{
					key:'pcHomepageUrl',
					title:'微应用管理后台',
					align:'center',
					render:(h,p) =>{
						return h('a',{
						domProps:{
							href:p.row.pcHomepageUrl
							}
						},p.row.pcHomepageUrl)
					}
				},{
					key:'brief',
					align:'center',
					title:"简介"
				},{
					key:'action',
					title:'审核状态',
					align:'center',
					render:(h,p)=>{
						let type = Number( p.row.status );
						let text = '审核中';
						switch(type){
							case 0:text = '草稿';break;
							case 1:text = '审核中';break;
							case 2:text = '审核通过';break;
							case 3:text =  p.row.refuseReason;break;
							case 4:text = '停用';break;
							case 5:text = '发布';break
						};
						if(type == 3){
							return h('div',[
							h('div',{
								style:{color:'red'} 
								},'审核拒绝'),
							h('div',text)
							])
						}
						return h('span',text)
					}
				},{
					key:'action',
					title:'管理',
					align:'center',
					render:(h,p) =>{
						let _this =this;
						return h('div',[
						h('Button',{
							props:{
								type:'info',
								size:'small'
							},
							style:{
								marginRight:'10px'
							},
							on:{
								click:()=>{
									_this.$router.push('/service/create?id='+p.row.id)

								}
							}
						},'修改'),
						h('Button',{
							props:{
								type:'error',
								size:'small'
							},
							
							on:{
								click:()=>{
									_this.$Modal.confirm({
										title:'提示',
										content:'确定要删除'+p.row.name,
										onOk(){
											_this.DATAFORM.request({url:'use/microapp/delete',data:{appId:p.row.id},method:"post"}).then(res =>{					_this.getlist()
												console.log(res)
												_this.$Message.success('删除成功')
											})
										}
									})
								
								}
							}
						},'删除')
						])
						
					}
				}],
				content_data:{
					data:[]
				}
			
			}
		},
		created() {
			this.getlist()
			
			
		},
		methods: {
			add_app(){
				this.$router.push('/service/create')
			},
			getlist(n=this.page){
				this.DATAFORM.request({url:'use/microapp/list',data:{pageNo:n,pageSize:10}}).then(res =>{
					this.content_data =res.data;
					console.log(res)
				})
			},
			pages(n){
				this.getlist(n-1)
			}
		},
		watch: {
			'$route' () {
				if( this.DATAFORM.dataform().addapp){
					this.getlist();
					this.DATAFORM.dataform({addapp:false})
				}
			}
		}
	}
</script>

<style>
	
	.service_header{
		margin: 0 auto;
		width: 100%;
		padding:30px 0;
		overflow: auto
	}
	
	.service_header .button_s{
		width: 100px;
		margin-left: 30rpx;
		float: right;
	}
	
	.service_content{
		width: 100%;
		padding: 30px;
	}
	
	@keyframes cardshow{
		0%{transform:scale(0.5)}
		100%{transform: scale(1);}
	}
	
	.cardshow-enter-active{
		animation: 0.2s cardshow linear;
	}
	
	.cardshow-leave-active{
		animation:cardshow 0.2s linear reverse
	}
</style>


