<template>
	<div class="service_content">
		<div class="service_header">
			<!--<Button class="button_s" @click="add_app">添加应用</Button>-->
		</div>
		
		<div>
			<Table  ref='contentlist' height='620' width='100%' :columns='content' :data='content_data' ></Table>
		</div>
			<transition name='cardshow'>
		<router-view></router-view>
			</transition>
	</div>
</template>

<script>
	export default {
		name: 'work',
		data() {
			return {
				content:[{
					key:'id',
					align:'center',
					title:"appid"
				},{
					key:'appSecret',
					align:'center',
					title:'appSecret'
				},{
					key:'name',
					align:'center',
					title:'姓名'
				},{
					key:'logo',
					title:'头像',
					align:'center',
					render:(h,p)=>{
						return h('Avatar',{props:{src:p.row.logo}})
					}
				},{
					key:"telephone",
					align:'center',
					title:'手机'
				},{
					key:'brief',
					align:'center',
					title:'简介'
				}],
				content_data:[]
			
			}
		},
		created() {
			this.getlist()
			
			
		},
		methods: {
			add_app(){
				this.$router.push('/work/create')
			},
			getlist(){
				this.DATAFORM.request({url:'use/get/current'}).then(res =>{
					this.content_data =res.data.data;
					console.log(res)
				})
			}
		},
		watch: {
			'$route' () {
				if( this.DATAFORM.dataform().addapp){
					this.getlist();
					this.DATAFORM.dataform({addapp:false})
				}
			}
		}
	}
</script>

<style>
	
	.service_header{
		margin: 0 auto;
		width: 100%;
		padding:30px 0;
		overflow: auto
	}
	
	.service_header .button_s{
		width: 100px;
		margin-left: 30rpx;
		float: right;
	}
	
	.service_content{
		width: 100%;
		padding: 30px;
	}
	
	@keyframes cardshow{
		0%{transform:scale(0.5)}
		100%{transform: scale(1);}
	}
	
	.cardshow-enter-active{
		animation: 0.2s cardshow linear;
	}
	
	.cardshow-leave-active{
		animation:cardshow 0.2s linear reverse
	}
</style>


