<template>
	<div class="login" > 
		<div class="bannerbox">
			<div class="banner"></div>
		</div>
		
		<!-- <div class="sub_header_box">
			<ul class="sub_header">
				<li class="on">同行都在用</li>
				<li>人力资源</li>
				<li>个性化定制</li>
				<li>市场营销</li>
				<li>财务</li>
				<li>CRM</li>
				<li>会务</li>
				<li>其他</li>
			</ul>
		</div> -->
		
		
		<div class="con_select_box">
			<ul class="con_select">
				<li v-for='item in app_list'>
					<img :src="item.logo" class="image"/>
					<div class="detail">
						<div class="title_s">{{item.name}}</div>
						<div class="subtitle_s"></div>
						<div class="text_tip">{{item.brief}}</div>
					</div>
					<div class="button_s" style='background: #8C939D;' v-if='item.hasInstall'>已安装</div>
					<div class="button_s" @click="shoping(item.id)" v-else>安装应用</div>
				</li>
				
			</ul>
		</div>
	</div>
</template>

<script>
	
	
export default {
	name:'login',
	data(){
		return {
			username:'',
			password:'',
			avail:0,
			app_list:[]
			
		}
	},
	created(){
		this.getdata()
		
		this.DATAFORM.dataform({_this:this})
	},
	mounted(){
		
	},
	methods:{
		shoping(id){
			this.DATAFORM.corpApp({url:'oa/microapp/choose',data:{ids:[id]},method:'post'}).then(res =>{
				this.getdata()
				this.$Message.success('添加成功')
			})
		},
		getdata(){
			this.DATAFORM.request({url:"microapp/shop"}).then(res =>{
				let developer = res.data.developer||[];
				this.app_list =developer
				
			})
		}
	}
}
</script>

<style >
.login{
	background: #f5f5f5;
	width: 100%;
}

</style>


