<template>

	<div id="app">
		<div v-if='nav_header' class="hellobox">
			<subnav></subnav>
			<router-view name='page'></router-view>
		</div>
		<div  v-else > 
		<vuenav></vuenav>
		<router-view  />
		</div>
		
		
	</div>
</template>

<script>
	import vuenav from './components/header.vue';
	import subnav from './components/HelloWorld.vue';
	export default {
		name: 'App',
		data() {
			return {
				nav_header:false	
			}
		},
		components: {
			vuenav,
			subnav
			
		},
		mounted() {

		},
		created() {
			this.routedata()
			let _this =this;
			window.onload=function(){
				_this.DATAFORM.dataform({_this:_this})
			}
		},
		methods: {
			routedata() {

				let wxlogin = this.DATAFORM.gettoken();
				
				if(!(wxlogin.token&&wxlogin.corpKey)){
					this.$router.push('/login')
				}
				
				let route = this.$route;
				
				if(route.name == 'login') {
					this.nav_header = false
				} else {
					this.nav_header = true
				}
			}
		},
		watch: {
			"$route" (to, from) {
				this.routedata();
				this.DATAFORM.dataform({_this:this})
			}
		}

	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
	}
	

	
	ul,
	ol,
	dl,
	dd,
	li {
		list-style: none;
	}
	
	
	
</style>


